import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Platform } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
import { ConceptModalPage } from '../concept-modal/concept-modal'

import GoogleImages from 'google-images'
import GettyImages	from 'gettyimages-api'
import * as d3 from 'd3';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  CSE_ID = '013227838598498082779:afpoqf-u6cu';
  API_KEY = 'AIzaSyA-SAyJyU4_Qg0BxNKEM0-Ec6iRsDNf7NM';

	gettyCredentials = {
		apiKey: "5zf3zt29mkfvjecece6kks2g",
		apiSecret: "uwWNXw2u2h77HFgQKxtmZNuFdSSKpcHp5R7zqh6V7FG28",
		username: "andrea.bellucci",
		password: "C1nd3r3ll4"
	}

	ROOT_NODE_RADIUS = 100;

  concept: string;
  googleSearchClient: any;
	gettyImagesClient: any;
  images:any;

	showSpinner: boolean;
  showImagesPanel:boolean;

  margin = {top: 20, right: 20, bottom: 30, left: 50};

  width: number;
  height: number;

  color: any;
  svg: any;
  g: any;

  simulation: any;
  graph = {
    "nodes": [],
    "links": [],
  };

  node:any;
  link:any;
  text:any;

  cnt:number;
  group: number;

  zoom: any;
	scale: number;
	k: number;

  constructor(public navCtrl: NavController,
              public platform: Platform,
              public modalCtrl: ModalController) {


    this.images = [];
		this.showSpinner = false;
    this.showImagesPanel = false;
    this.googleSearchClient = new GoogleImages(
                                this.CSE_ID,
                                this.API_KEY);

		this.gettyImagesClient = new GettyImages(this.gettyCredentials);
		this.gettyImagesClient.getAccessToken(function(err, response) {
    if (err) throw err
    console.log(response.access_token);
});

    this.concept = "";
    this.cnt = 1;
    this.group = 1;

    this.zoom = d3.zoom()
			.scaleExtent([0.25, 4])
			.on("zoom", this.zoomed.bind(this));

  }

  ionViewDidLoad() {
    this.platform.ready().then((readySource) => {
      this.width = this.platform.width() - this.margin.left - this.margin.right ;
      this.height = this.platform.height() - this.margin.top - this.margin.bottom;
      this.initSvg();
    });

  };

	selectImage ( ev ){
		console.log( ev );
	}

	onInput( ){
		if(this.concept === ""){
					this.images = [];
					this.showImagesPanel = false;
					return;
		}

		this.searchImages()
	};

	/*handleFile( ev ){
		var file = ev.srcElement.files[0];
		console.log(file);
		var img = document.createElement("img");
	 		img.classList.add("obj");
	 		img.file = file;
			document.getElementById("addedImage").style.visibility = "visible";
	 		document.getElementById("addedImage").appendChild(img);
			 var reader = new FileReader();
			 reader.onload = (function(aImg) { return function(e) { aImg.src = e.target.result; }; })(img);
			 reader.readAsDataURL(file);
	};
	*/

  searchImages(  ){
    var self = this;
		this.showSpinner = true;
		this.showImagesPanel = true;
		this.images = [];
		/*for(var i = 0; i < 10; i++){
			self.images[i] = {thumbnail: { url: 'https://c1.staticflickr.com/9/8287/29517736620_3184b66ec8.jpg'}}
		}
		self.showSpinner = false;
		*/


	  this.googleSearchClient.search( this.concept )
      .then(images => {
				self.showSpinner = false;
        self.images = images;
      })
			.catch(err => {
				self.showSpinner = false;
				self.showImagesPanel = true;
				for(var i = 0; i < 10; i++){
					self.images[i] = {thumbnail: { url: 'https://c1.staticflickr.com/9/8287/29517736620_3184b66ec8.jpg'}}
				}

			});

  };

  startWeb( event ){
    this.showImagesPanel = false;

    if(this.concept.trim() === "") return;

    this.svg.selectAll("*").remove();
    this.svg.append('svg:defs')
      .append("svg:pattern")
        .attr("id", "fill-0")
        .attr("width", this.ROOT_NODE_RADIUS * 2)
        .attr("height", this.ROOT_NODE_RADIUS * 2)
        .attr("patternUnits", "userSpaceOnUse")
        .append("svg:image")
				.attr("x", 0)
        .attr("y", 0)
        .attr("xlink:href", event.srcElement.src)
        .attr("width", this.ROOT_NODE_RADIUS * 2)
        .attr("height", this.ROOT_NODE_RADIUS * 2)
				.attr("preserveAspectRatio", "xMidYMid slice");
				// build the arrow.


    this.graph.nodes = [];
    this.graph.links = [];
    this.simulation = d3.forceSimulation()
        .force("link", d3.forceLink().id(function(d:any) { return d.id; }).distance(this.ROOT_NODE_RADIUS * 2).strength(1))
        .force("charge", d3.forceManyBody())
        .force("collision", d3.forceCollide(this.ROOT_NODE_RADIUS * 2))
        .force("center", d3.forceCenter((this.width / 2) + this.ROOT_NODE_RADIUS * 2, (this.height / 2) + this.ROOT_NODE_RADIUS * 2));

    this.graph.nodes[0] = {
      "id": 0,
      "label": this.concept,
      "img_url": event.srcElement.src,
      "group": 1,
      "fx": this.width / 2,
      "fy": this.height / 2,
    };

		this.link = this.svg.append("g")
				.attr("class", "links")
			.selectAll("line")
			.data(this.graph.links)
			.enter().append("line")

		this.node = this.svg.append("g")
        .attr("class", "nodes")
      .selectAll("circle")
      .data(this.graph.nodes)
      .enter().append("circle")
        .attr("r", this.ROOT_NODE_RADIUS)
				.attr("cx", this.ROOT_NODE_RADIUS)
				.attr("cy", this.ROOT_NODE_RADIUS)
        .style("fill", "url(#fill-0)")
				.style("stroke-width", 3)

        .call(d3.drag()
            .on("start", this.dragstarted.bind(this))
            .on("drag", this.dragged.bind(this))
            .on("end", this.dragended.bind(this)))
        .on('click', this.clicked.bind(this));

    this.text = this.svg.append("g").attr("class", "labels").selectAll("g")
        .data(this.graph.nodes)
      .enter()
			.append("text")
      .attr("y", -this.ROOT_NODE_RADIUS - 5)
			.attr("x", 0)
			.style("text-anchor", function(d) { return "middle"; })
      .text(function(d) { return d.label; });


    this.simulation
        .nodes(this.graph.nodes)
        .on("tick", this.ticked.bind(this));

    this.simulation.force("link")
        .links(this.graph.links);
  }

  dragstarted( d ){
    d3.event.sourceEvent.stopPropagation();
    if(d.id === 0) return;
    if (!d3.event.active) this.simulation.alphaTarget(0.3).restart();
    d.fx = d.x;
    d.fy = d.y;
  }

  dragged( d ){
    if(d.id === 0) return;
      d.fx = d3.event.x;
      d.fy = d3.event.y;
  }

  dragended( d ){
    d3.event.sourceEvent.stopPropagation();
    if(d.id === 0) return;

    if (!d3.event.active) this.simulation.alphaTarget(0);
    d.fx = null;
    d.fy = null;
  }

  ticked( ){
		this.link
				.attr("x1", function(d) { return d.source.x; })
				.attr("y1", function(d) { return d.source.y; })
				.attr("x2", function(d) { return d.target.x; })
				.attr("y2", function(d) { return d.target.y; });

		this.node
				.attr("transform", (d) => {
					return "translate(" + (d.id === 0 ? d.x - this.ROOT_NODE_RADIUS : d.x - (this.ROOT_NODE_RADIUS / 2)) + ","
					+ (d.id === 0 ? d.y - this.ROOT_NODE_RADIUS : d.y - (this.ROOT_NODE_RADIUS / 2)) + ")";
					});

		this.text
       .attr("transform", function(d) { return "translate(" + (d.x) + "," + (d.y) + ")"; });
  }

  clicked ( d ){
    if (d3.event.defaultPrevented) return;

		var modal = this.modalCtrl
			.create(ConceptModalPage, { parentConcept: this.concept })

		modal.onDidDismiss(data => {
			//console.log(data);
			if(data == null) return;
			if(d.group === 1) this.group++;
			var n = {	id: ""+ this.cnt++,
								group: d.group === 1 ? this.group : d.group,
								label: data.label,
								img_url: data.img_url }
			this.graph.nodes.push(n);
			this.graph.links.push({source: d.id, target: n.id, group: n.group});

			this.svg.select("defs")
			.append("svg:pattern")
			.attr("id", "fill-"+n.id)
			.attr("width", this.ROOT_NODE_RADIUS)
			.attr("height", this.ROOT_NODE_RADIUS)
			.attr("patternUnits", "userSpaceOnUse")
			.append("svg:image")
			.attr("x", 0)
			.attr("y", 0)
			.attr("xlink:href", n.img_url)
			.attr("width", this.ROOT_NODE_RADIUS)
			.attr("height", this.ROOT_NODE_RADIUS)
			.attr("preserveAspectRatio", "xMidYMid slice");

			this.svg.select('defs')
				.append('marker')
					.attr('id','arrowhead-'+n.id)
					.attr(  'viewBox','-0 -5 10 10')
					.attr(  'refX',24)
					.attr(  'refY',0)
					.attr(  'orient','auto')
					.attr(  'markerWidth',12)
					.attr(  'markerHeight',12)
					.attr(  'xoverflow','visible')
					.append('svg:path')
					.attr('d', 'M 0,-5 L 10 ,0 L 0,5')
					.attr('fill', this.color(n.group))
					.style('stroke','none');

			this.restart();
	   });

		modal.present();

  }

 flatten(root) {
  	var nodes = [], i = 0;

	  function recurse(node) {
	    if (node.children) node.children.forEach(recurse);
	    if (!node.id) node.id = ++i;
	    nodes.push(node);
	  }

	  recurse(root);
	  return nodes;
	}


  restart(){
    var self = this;
    this.link = this.link.data(this.graph.links, function(d) {return d.source.id + "-" + d.target.id; });
    this.link.exit().remove();
    this.link = this.link.enter()
			.append("line")
				.style("stroke", d => {return self.color( d.group )})
				.style("stroke-width", 3)
				.attr("class", "link")
				.attr('marker-end',d => {return 'url(#arrowhead-'+d.target+')'}).merge(this.link);

    this.node = this.node.data(this.graph.nodes, function(d) { return d.id;});
    this.node.exit().remove();
    this.node = this.node.enter()
      .append("circle")
        //.attr("fill", function(d) { return self.color( d.group ); })
				.style("fill", d => {return "url(#fill-" + d.id + ")"})
				.style("stroke", d => {  return self.color( d.group )})
				.style("stroke-width", 3)

				.attr("cx", d => {return (d.id === 0) ? self.ROOT_NODE_RADIUS : self.ROOT_NODE_RADIUS / 2})
				.attr("cy", d => {return (d.id === 0) ? self.ROOT_NODE_RADIUS : self.ROOT_NODE_RADIUS / 2})
        .attr("r", d => {return (d.id === 0) ? self.ROOT_NODE_RADIUS : self.ROOT_NODE_RADIUS / 2}).merge(this.node)
        .call(d3.drag()
            .on("start", this.dragstarted.bind(this))
            .on("drag", this.dragged.bind(this))
            .on("end", this.dragended.bind(this)))
          .on('click', this.clicked.bind(this));

          //.on('dblclick', this.dblclicked.bind(this));
			this.text = this.text.data(this.graph.nodes, d => {return d.label});
			this.text.exit().remove();
			this.text = this.text.enter()
			.append("text").merge(this.text)
			.attr("y", d => { return (d.id === 0) ? -self.ROOT_NODE_RADIUS - 5 : -(self.ROOT_NODE_RADIUS/2)-5})
			.attr("x", 0)
			.style("text-anchor", "middle")
			.text( d => d.label);


    // Update and restart the simulation.
    this.simulation.nodes(this.graph.nodes);
    this.simulation.force("link").links(this.graph.links);
    this.simulation.alpha(1).restart();

  }


  initSvg(){
    this.color = d3.scaleOrdinal(d3.schemeCategory20);

    this.svg = d3.select("#concept-web").append("svg")
    	.attr("width",  this.width)
    	.attr("height", this.height)

		this.svg.call(this.zoom);


  }

	zoomed( ){
		this.svg.selectAll('g').attr('transform', d3.event.transform);
	}

	zoomIn( ev ){
		this.transition(1.25);
	}

	zoomOut( ev ){
		this.transition(0.75);
	}

	transition(zoomLevel) {
	  this.svg.transition()
	      .delay(50)
	      .duration(400)
	      .call(this.zoom.scaleBy, zoomLevel);
	      //.call(zoom.transform, transform);
	      //.on("end", function() { canvas.call(transition); });
	}
}



