import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

import GoogleImages from 'google-images'

/**
 * Generated class for the ConceptModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-concept-modal',
  templateUrl: 'concept-modal.html',
})
export class ConceptModalPage {
	CSE_ID = '013227838598498082779:afpoqf-u6cu';
  API_KEY = 'AIzaSyA-SAyJyU4_Qg0BxNKEM0-Ec6iRsDNf7NM';
	parentConcept:string;
	images:any;
	label:any;
	img_url:any;

	showSpinner: boolean;
  showImagesPanel:boolean;
	googleSearchClient: any;


  constructor(public navCtrl: NavController,
		public viewCtrl: ViewController,
		public navParams: NavParams) {
			this.images = [];
			this.label = "";
			this.img_url = "";
			this.parentConcept = this.navParams.get('parentConcept');
			this.googleSearchClient = new GoogleImages(
																	this.CSE_ID,
																	this.API_KEY);

  }

  ionViewDidLoad() {

    console.log('ionViewDidLoad ConceptModalPage');
  }

	dismiss( ){
		if(this.label != ""){
			this.viewCtrl.dismiss( {label: this.label, img_url: this.img_url} );
		}



	}

	onInput( ){
		if(this.label === ""){
					this.images = [];
					this.showImagesPanel = false;
					return;
		}

		this.searchImages()
	};

  searchImages(  ){
		var self = this;
		this.showSpinner = true;
		this.showImagesPanel = true;
		this.images = [];
		/*for(var i = 0; i < 10; i++){
			self.images[i] = {thumbnail: { url: 'https://c1.staticflickr.com/9/8287/29517736620_3184b66ec8.jpg'}}
		}
		self.img_url = self.images[0].thumbnail.url;
		self.showSpinner = false;
*/
  this.googleSearchClient.search( this.label )
      .then(images => {
				self.showSpinner = false;
        self.images = images;
      })
			.catch(err => {
				self.showSpinner = false;
				self.showImagesPanel = true;
				for(var i = 0; i < 5; i++){
					self.images[i] = {thumbnail: { url: 'https://c1.staticflickr.com/9/8287/29517736620_3184b66ec8.jpg'}}
				}
				self.img_url = self.images[0].thumbnail.url;

			});
  };

}



